
.table th,.table td{border:0 !important
}
.simplert__title{color:#000 !important
}
.simplert__body{color:#000 !important
}
label.matrix_ques{margin:0.4em 0 0.7em 0
}
.bg-color{background:#312450
}
.headerColor{background:#312450
}
.matrix_content{margin:auto;width:1500px;max-width:100%
}
.increse_index{position:absolute;z-index:1 !important
}
.slider_index{z-index:1000 !important
}
.question_content{height:400px;overflow-y:scroll;overflow-x:hidden
}
.swiper-container{z-index:0 !important
}
.swiper-wrapper{z-index:0 !important
}
.hg-100{height:100%;min-height:100%;background-repeat:no-repeat;background-size:cover;background-position:center center
}
.header-style{color:#000;font-weight:bold;margin-top:0px
}
.outer_content{background-repeat:no-repeat;background-size:cover;background-position:center center;min-height:100%;width:auto;overflow:hidden !important
}
.slider{width:200px;z-index:1000
}
.emo_1 .vue-slider .vue-slider-dot{background:url("../../static/disappointed.svg") no-repeat !important;background-size:contain;box-shadow:none;border-radius:0
}
.emo_2 .vue-slider .vue-slider-dot{background:url("../../static/worried.svg") no-repeat !important;background-size:contain;box-shadow:none;border-radius:0
}
.emo_3 .vue-slider .vue-slider-dot{background:url("../../static/neutral_face.svg") no-repeat !important;background-size:contain;box-shadow:none;border-radius:0
}
.emo_4 .vue-slider .vue-slider-dot{background:url("../../static/smiling.svg") no-repeat !important;background-size:contain;box-shadow:none;border-radius:0
}
.emo_5 .vue-slider .vue-slider-dot{background:url("../../static/veryhappy.svg") no-repeat !important;background-size:contain;box-shadow:none;border-radius:0
}
.wrapper.fullscreen{display:-moz-flex;display:-ms-flex;display:-ms-flexbox;display:flex;-moz-flex-direction:column;-ms-flex-direction:column;flex-direction:column;-moz-justify-content:center;-ms-justify-content:center;-ms-flex-pack:center;justify-content:center;min-height:100vh;background-size:cover
}
.parallax-bg{position:absolute;left:0;top:0;width:130%;height:100%;-webkit-background-size:cover;background-position:center;background-color:rgba(0,0,0,0.6);background-image:url(/static/images/surmon-1.jpg)
}
#bg{position:fixed;top:0;left:0;width:100%;height:100%;min-width:50%;min-height:50%
}
.overlay{width:100%;height:100%;box-shadow:inset 0 0 0 2000px rgba(0,0,0,0.7)
}
.parallax{left:0;top:0;width:130%;height:100%;-webkit-background-size:cover;background-position:center
}
.position_gear{cursor:pointer;background:#fff;border-radius:10px;position:absolute;top:10px;right:10px;padding:2px;color:#fff;font-weight:50px;width:20px;height:20px;z-index:1000
}
.position_offline{cursor:pointer;border-radius:10px;position:absolute;top:10px;right:35px;padding:2px;color:#fff;font-weight:50px;width:20px;height:20px;z-index:1000
}
html{height:100%
}
body{min-height:100% !important;height:100%
}
.rangeInput.large{position:absolute;margin-top:34px
}
.rangeInput[type="radio"]:before{position:relative;display:block;width:30px;height:30px;border-radius:100%;content:""
}
.rangeInput[type="radio"]:after{position:relative;display:block;left:0px;top:0px;width:7px;height:7px;-moz-border-radius:50px;-webkit-border-radius:50px;border:5px solid #AAAAAA;border-radius:100%;content:"";background-image:linear-gradient(135deg, #fff 0%, #fff 100%);background-repeat:no-repeat;background-position:center
}
.rangeInput[type="radio"]:not(:disabled):hover:after{background-image:linear-gradient(135deg, #8BB0C2 0%, #fff 100%);border-color:#2196F3
}
.rangeInput[type="radio"]:not(:disabled):hover:before{border-color:#3D7591
}
.rangeInput.large{height:30px;width:30px
}
.rangeInput.large[type="radio"]:before{height:30px;width:30px
}
.rangeInput.large[type="radio"]:after{top:-48px;width:55px;height:55px
}
.rangeInput.large.custom[type="radio"]:checked:after{background-size:100%;border-color:#2196F3
}



